/* General styles */

body {
    background-color: #f5f5f5;
    font-family: Arial, sans-serif;
    font-size: 16px;
  }
  
  h1 {
    text-align: center;
    margin-top: 50px;
    margin-bottom: 20px;
    font-size: 3rem;
    font-weight: bold;
    color: #333;
  }
  
  input {
    border-radius: 5px;
    border: none;
    padding: 10px;
    font-size: 1rem;
    width: 100%;
    margin-bottom: 20px;
  }
  
  button {
    border-radius: 5px;
    border: none;
    padding: 10px;
    font-size: 1rem;
    width: 100%;
    background-color: #4f6a88;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  button:hover {
    background-color: #7b9bbd;
  }
  
  .form-link {
    text-align: center;
    margin-top: 20px;
  }
  
  .form-link span {
    color: #333;
  }
  
  /* Specific styles for this form */
  
  #signup-form {
    max-width: 500px;
    margin: 0 auto;
    background-color: #fff;
    padding: 40px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  }
  
  .field {
    position: relative;
  }
  
  .field label {
    position: absolute;
    top: 10px;
    left: 10px;
    color: #999;
    font-size: 1rem;
    transition: all 0.3s ease;
    pointer-events: none;
  }
  
  .field input:focus + label,
  .field input:not(:placeholder-shown) + label {
    top: -20px;
    font-size: 0.8rem;
    color: #7b9bbd;
  }
  
  .field input:focus,
  .field input:active {
    outline: none;
    box-shadow: 0px 0px 5px rgba(123, 155, 189, 0.5);
  }

  
  .Login-btn {
    border-radius: 5px;
    border: none;
    padding: 10px;
    font-size: 1rem;
    width: 30%;
    background-color: #4f6a88;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s ease;
    margin: auto;
    margin-top: 20px;
    text-align: center;
    text-decoration: none;
  }
  
  .Login-btn:hover {
    background-color: #7b9bbd;
  }
  