/* General styles */

body {
    background-color: #f5f5f5;
    font-family: Arial, sans-serif;
    font-size: 16px;
  }
  
  h1 {
    text-align: center;
    margin-top: 50px;
    margin-bottom: 20px;
    font-size: 3rem;
    font-weight: bold;
    color: #333;
  }
  
  input {
    border-radius: 5px;
    border: none;
    padding: 10px;
    font-size: 1rem;
    width: 100%;
    margin-bottom: 20px;
  }
  
  button {
    border-radius: 5px;
    border: none;
    padding: 10px;
    font-size: 1rem;
    width: 100%;
    background-color: #4f6a88;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  button:hover {
    background-color: #7b9bbd;
  }
  
  .form-alert {
    color: #ff0000;
    margin-top: 10px;
  }
  
  .btn {
    border-radius: 5px;
    border: none;
    padding: 10px;
    font-size: 1rem;
    width: 100%;
    background-color: #333;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-top: 20px;
  }
  
  .btn:hover {
    background-color: #666;
  }
  
  /* Specific styles for this form */
  
  form {
    max-width: 500px;
    margin: 0 auto;
    background-color: #fff;
    padding: 40px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  }
  
  .form-row label {
    color: #999;
    font-size: 1rem;
    margin-bottom: 10px;
  }
  
  .form-row input:focus {
    outline: none;
    box-shadow: 0px 0px 5px rgba(123, 155, 189, 0.5);
  }

  .register-btn {
    border-radius: 5px;
    border: none;
    padding: 10px;
    font-size: 1rem;
    width: 30%;
    background-color: #4f6a88;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s ease;
    margin: auto;
    margin-top: 20px;
    text-align: center;
  }
  
  .register-btn:hover {
    background-color: #7b9bbd;
  }
  
  